export default {
  methods: {
    setCurrentTimeMs(player) {
      // 时间轴点击事件
      this.$bus.$on('setCurrentTimeMs', (ms) => {
        let s = ms / 1000
        player.currentTime = s
        this.$store.commit('setVideoPlayOrPasu', false)
      })
    },
    setStartTimeMs(player) {
      // 时间设置跳转时间
      this.$bus.$on('setStartTimeMs', (ms) => {
        let s = ms / 1000
        player.currentTime = s
      })
    }
  },
  mounted() {}
}
